<template>
  <v-container class="mt-10">
    <div class="authprev">
      <v-card class="pa-3">
        <h3>new account</h3>
        <v-divider class="ma-3"></v-divider>
        <v-form ref="signup">
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="username"
              outlined
              v-model="username"
              dense
              rounded
              prepend-inner-icon="mdi-account-outline"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              :label="email"
              outlined
              v-model="email"
              dense
              rounded
              prepend-inner-icon="mdi-email-outline"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="password"
              outlined
              dense
              rounded
              v-model="password"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
              hint="At least 8 characters"
              counter
              prepend-inner-icon="mdi-key-outline"
              @click:append="show1 = !show1"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0 text-center">
            <v-btn
              class="col-md-12"
              dense
              rounded
              color="success"
              @click="handleSubmit"
              :dark="!loading"
              elevation="3"
              :loading="loading"
            >
              <v-icon>mdi-plus-outline</v-icon>create now
            </v-btn>
          </v-col>
        </v-form>
        <v-divider class="ma-3"></v-divider>
      </v-card>
    </div>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      show1: false,
      show2: true,
      show3: false,
      show4: false,
      password: "",
      email: "",
      username: "",

      loading: false,
    };
  },

  methods: {
    successNotification(text) {
      this.$notify({
        group: "success",
        type: "success",
        title: "success",
        text: text,
      });
    },
    errorNotification(text, type = "error") {
      this.$notify({
        group: "errors",
        type: type,
        title: "error",
        text: text,
      });
    },

    handleSubmit() {
      this.loading = true;

      this.$store
        .dispatch("signUp", {
          username_u: this.username,
          password: this.password,
          email: this.email,
        })
        .then((resul) => {
          this.loading = false;
          // resul.status === 200 &&
          if (resul.data.code == 200) {
            this.$router.push({ name: "Login" });
          } else {
            alert("sks");
            this.$notify({
              group: "errors",
              type: "error",
              title: "error",
              text: "error",
            });
          }
        })
        .catch((err) => {
          alert(err.message);
          this.$notify({
            group: "errors",
            type: "error",
            title: "error",
            text: err.message,
          });

          this.loading = false;
        });
    },
  },
};
</script>
